import React from "react"

export const List = ({
    variant,items
})=>{

    const ListComponent = variant ==='ordered'?'ol':'li';

 

    return (
        <div>
            <ul className="list-disc pl-4">
                {items.map((item,index)=>{
                    // console.log(item)
                    <li className="font-bold text-black" key={index}>{item}</li>
                })}
            </ul>     
        </div>
    )
}